.copyright__link {
  width: 100%;
  text-decoration: none;
  font-family: @font-family;
  font-weight: 400;
  font-size: 16px;
  color: @special-dark-grey;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &:hover path,
  &:focus path {
    fill: @basic-green;
  }
  &:active,
  &:active path {
    fill: @basic-green;
    opacity: 0.3;
  }
  @media (min-width: @tablet-width) {
    justify-content: flex-end;
  }
}

.copyright__link svg {
  width: 27px;
  height: 34px;
  @media (min-width: @tablet-width) {
    margin-left: 22px;
  }
}
